* {
    padding: 0;
    margin: 0;

}

/* i rem=10px */
body {
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
}

html,
body {
    font-size: 62.5%;
    height: 100%;

}

html {
    background: #eee;

}

.box {
    width: 20vw;
    height: 80vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
    min-width: 20rem;
    min-height: 35rem;
}



.info {
    position: absolute;
    bottom: 0;
    font-weight: 800;
    width: 100%;
    height: 45%;
    z-index: 4;

}

.location {
    margin-top: 0.1rem;
    text-align: center;
    font-weight: 800;
    font-size: 3rem;
    font-weight: 600;
}

.fas-street-view {
    animation: rotates 3s linear infinite alternate;
}

@keyframes rotates {
    from {
        transform: translate(-0.5rem);
    }

    to {
        transform: translate(0.5rem);
    }
}

#date {
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}

.temp {
    margin-top: 2.5em;
    text-align: center;
    font-size: 4rem;

}

.tempmin_max {
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #57606f;
}

#weathericon {
    height: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}



#weathericon .fas {
    font-size: 6rem;
    animation: fas-anime 3s linear infinite alternate;
}


@keyframes fas-anime {
    from {
        transform: scale(1.1);
    }

    to {
        transform: scale(1.8);
    }
}


@media(max-width:600px) {
    .box {
        width: 90vw;
        height: 80vh;
    }

    #weathericon {
        font-size: 5em;
    }

    .info {
        font-size: 1.5rem;
    }
}

@media(max-width:500px) {
    .box {

        height: 80vh;
    }

}

body>span {
    width: 100vw;
    text-align: center;
    color: grey;
}